<template>
    <div ref="about" id="about" class="grid grid-cols-2 bg-cover bg-no-repeat bg-top"
         style="background-image: url(img/about-bg.jpg)">
        <div class="lg:col-span-1 col-span-2 bg-cover bg-top 2xl:bg-top md:bg-right-bottom"
             style="background-image: url(img/about-blue-bg.svg)">
            <div class="flex items-center h-96 md:ml-20 ml-2 mt-20">
                <div class="flex-initial">
                    <h1 class="text-5xl text-white PattayaFont title">About</h1>
                    <p class="NotoSansFont font-medium text-white">
                        <br/>
                        
                        <br/>
                        
                        <br/>
                        </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { gsap }  from "gsap";
import { ScrollToPlugin } from 'gsap/ScrollToPlugin'// gsapScrollToPlugin
import { ScrollTrigger } from 'gsap/ScrollTrigger'// gsapScrollToPlugin
gsap.registerPlugin(ScrollToPlugin, ScrollTrigger)// 
export default {
    methods: {
        startAnimate() {
            let start = gsap.timeline({
                paused: true,
                scrollTrigger: {
                    trigger: ".box",
                    start: "top top",
                    end: "center center",
                    // 
                    // markers: true,
                    //  
                    // scrub: true,
                    toggleActions: "restart none resume pause",
                    pin: false
                }
            });
            start.fromTo(this.$refs.about, { opacity: 0, scale: 2, rotationX: 0, y: "-=100" }, { opacity: 1, scale: 1, rotationX:360, y: 0, duration: 1, imediateRender: false });
            start.play();
        }
    },
    mounted() {
        this.startAnimate();
    }
}
</script>
